.page {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0;
  font-family: 'Inter', ‘Arial’, sans-serif;
  background-color: #000;
  color: #fff;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
}